import Vue from 'vue'
import Router from 'vue-router'
import Home from "@/components/Home"
import BasicFunc from "@/components/pages/BasicFunc"

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/basic_func',
      name: 'BasicFunc',
      component: BasicFunc
    }
  ]
})
